import groupScollX from "@/components/mycom/goodWorkers.vue"
import groupSend from "@/components/mycom/groupSend.vue"
import maps from "qqmap"
export default {
	components: {
		groupScollX,
		groupSend
	},
	data() {
		return {
			id:'',
			desc:'',
			map:null,
			dataObject: {
				groupName: "", //商品名称
				desc: "", //公司描述
				address: "", //位置
				details: "", //  详细地址
				lng: "40.013305", //维度
				lat: "118.685713", //经度
				sendPerson: [{ //发布人列表
					id: "", //发布人id
					name: "", // 发布人名称
					works: "", // 职位
					phone: "", //  联系方式
				}],
				dataList: [{ //优秀员工列表
					id: "", //优秀员工id
					name: "", // 优秀员工名称
					works: "" // 职位
				}]
			}

		}
	},
	onLoad(options) {
		this.id=options.id;
		this.$nextTick(function() {
			this.loadData()
		})
	},
	onShow() {
		if(this.$wechat.isWechat()){
		    this.$wechat.wxShare(); 
		} 
	},
	methods: {
		loadData() {
			let parmas = {
				id: this.id,
				uid: getApp().globalData.uid
			}
			this._reqw.ipost(parmas, 'groupDetail').then(res => {
				res.result == 0 ?(this.dataObject=res,this.$refs.groupScollX.set(res.dataList),this.$refs.groupSend.set(res.sendPerson),this.desc=this.$api.formText(res.desc)):this.$api.tip(res.resultNote)
			}).catch(err=>{})
		}
	}
}
